import {NavLink} from 'react-router-dom';
import s from './Navbar.module.css'
import React from "react";
import Friends from "./Friends/Friends";
import { FriensType } from '../../types/types';

type PropsType = {
    friends: Array<FriensType>
}

const Navbar: React.FC<PropsType> = (props) => {
    return (
        <div className={s.nav}>
            <div>
                <NavLink className={({isActive}) => isActive ? s.active : s.item} to='/profile'> </NavLink>
            </div>
            <div>
                <NavLink className={({isActive}) => isActive ? s.active : s.item} to='/dialogs'></NavLink>
            </div>
            <div>
                <NavLink className={({isActive}) => isActive ? s.active : s.item} to='/news'></NavLink>
            </div>
            <div>
                <NavLink className={({isActive}) => isActive ? s.active : s.item} to='/music'></NavLink>
            </div>
            <div>
                <NavLink className={({isActive}) => isActive ? s.active : s.item} to='/settings'></NavLink>
            </div>
            <div>
                <NavLink className={({isActive}) => isActive ? s.active : s.item} to='/users'></NavLink>
            </div>
            <Friends friends={props.friends} />
        </div>
    );
}

export default Navbar